import React from 'react';
import { Layout, Menu } from 'antd';
import { Switch, Route, Link, Redirect, useLocation } from 'react-router-dom';
import { Main, Project, EditProject, CreateProject, EditUser, CreateUser, User } from './index';
import UserBlock from '../components/UserBlock';
//Redux
import { useSelector } from 'react-redux';

const { Header, Content } = Layout;

const Home = ({ onSuccessLogout }) => {
	let location = useLocation();
	const { userInfo } = useSelector(({ projects }) => projects);

	return (
		<Layout>
			<Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
				<Menu
					theme="dark"
					mode="horizontal"
					defaultSelectedKeys={[location.pathname]}
					style={{ width: 360 }}>
					<Menu.Item key="/">
						<Link to="/">Главная</Link>
					</Menu.Item>
					{
						//Админ
						userInfo.isadmin === 1 && (
							<>
								<Menu.Item key="/projects">
									<Link to="/projects">Проекты</Link>
								</Menu.Item>
								<Menu.Item key="/users">
									<Link to="/users">Пользователи</Link>
								</Menu.Item>
							</>
						)
					}
				</Menu>
				<UserBlock onLogout={onSuccessLogout} />
			</Header>
			<Content className="site-layout">
				<Switch>
					<Route exact path="/">
						<Main />
					</Route>
					{
						//Админ
						userInfo.isadmin === 1 && (
							<Switch>
								<Route exact path="/projects">
									<Project />
								</Route>
								<Route exact path="/projects/:id/edit">
									<EditProject />
								</Route>
								<Route exact path="/projects/create">
									<CreateProject />
								</Route>
								<Route exact path="/users">
									<User />
								</Route>
								<Route exact path="/users/:id/edit">
									<EditUser />
								</Route>
								<Route exact path="/users/create">
									<CreateUser />
								</Route>
								<Redirect to="/" />
							</Switch>
						)
					}
					<Redirect to="/" />
				</Switch>
			</Content>
		</Layout>
	);
};

export default Home;
